import React from 'react'
import Profile from '../../Public/Images/Profile2.jpg'
import { NavLink } from 'react-router-dom'

const Navbar = () => {
  return (
    <>
    <div className='flex justify-between p-5 bg-white w-full fixed z-10 shadow-lg'>

        <div id='leftnav' className='flex bg-white gap-5'>
            <img src={Profile}  className='rounded-full w-32 h-32 border-custom-purple border-8' />
            <div className='flex flex-col justify-center '>
                <h1 className='text-4xl font-sans hover:text-purple-500 cursor-pointer'> </h1>
            </div>
        </div>

        <div id='rightnav' className='flex gap-36 text-3xl font-semibold cursor-pointer p-10'>
            <div>
                <NavLink to='/home'  className='hover:text-purple-500 hover:text-4xl transition-all duration-500'>Home</NavLink>
            </div>
            <div>
                <NavLink to='/about' className='hover:text-purple-500 hover:text-4xl transition-all duration-500'>About</NavLink>
            </div>
            <div>
                <NavLink to='/projects' className='hover:text-purple-500 hover:text-4xl transition-all duration-500'>Projects</NavLink>
            </div>
            <div>
                <NavLink to='/contact' className='hover:text-purple-500 hover:text-4xl transition-all duration-500'>Contact</NavLink>
            </div>
        </div>

    </div>
    </>
  )
}

export default Navbar
