import React from 'react'
import { faLinkedin , faGithub , faInstagram } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const Footer = () => {
  return (
    <>
    <div id='footer' className='bg-black text-white p-44'>

        <div className='flex justify-between'>
            <div id='left-footer'>
                <h1 className='uppercase text-7xl'>ahmed eissa</h1>
                <p className='text-2xl w-2/3 pt-10'>A Fullstack focused Web Developer building the Frontend  and Backend of Websites and Web Applications that leads to the success of the overall product</p>
            </div>
            <div id='right-footer' >
                <h1 className='text-4xl uppercase'>social</h1>
                <div className='mt-10'>
                    <FontAwesomeIcon icon={faLinkedin} className='text-6xl'/>
                    <FontAwesomeIcon icon={faGithub}  className='text-6xl px-10'/>
                    <FontAwesomeIcon icon={faInstagram}  className='text-6xl'/>
                </div>
            </div>
        </div>

        <div className='mt-32'>
            <hr className='text-white bg-white w-full h-1'/>
        </div>

        <div className='flex justify-center mt-20'>
        	<p className='text-xl'>&copy;  Copyright 2024 . Made by <a href="#" className='underline'>Ahmed Eissa</a></p>
        </div>

    </div>  
    </>
  )
}

export default Footer
