import React from 'react'
import Example from '../../../Public/Images/Untitled design.png'
import { NavLink } from 'react-router-dom'

const Bistro = () => {
  return (
    <>
    <div id='bistro' className='pt-56 bg-gray-100'>

    <div id='header' className='text-center p-16'>
            <h1 className='text-7xl font-bold font-sans uppercase'>Bistro Bliss</h1>
        <div id='about-paragraph' className='flex justify-center'>
            <p className='text-4xl text-gray-600 font-sans w-6/12 my-12'>This page contains the case study of Bistro Bliss Open-Source Project which includes the Project Overview, Tools Used and Live Links to the official product.</p>
        </div>
        <div className='mt-10'>
          <NavLink to='https://github.com/3issajr/Bistro-Bliss' className='uppercase bg-custom-purple text-white p-8 w-2/12 rounded-lg text-3xl font-bold text-center'>Project Link</NavLink>
        </div>
    </div>

    <div id='bistro-body' className='p-56 bg-white w-screen flex justify-center '>

      <div className='w-7/12 h-7/12'>
        <img src={Example} className='w-full'/>
        <div className='flex flex-col mt-16'>
          <h1 className='py-4 text-5xl font-bold'>Project Overview</h1>
          <p className='py-10 text-3xl text-gray-600 font-sans leading-snug'>Bistro Bliss is an e-commerce platform designed to offer a seamless food ordering experience for customers. The platform aims to provide a wide range of food options from various restaurants and eateries, allowing users to explore and order their favorite dishes with ease.</p>
          <p className='py-4 text-3xl text-gray-600 font-sans'>Feel free to check out the Project by visiting the Project Link.</p>
        </div>

      <div id='tools' className='flex flex-col mt-16'>
        <div>
          <h1 className='text-5xl font-bold'>Tools Used</h1>
        </div>
          <div className='flex gap-5 text-gray-600 font-semibold text-2xl mt-10'>
            <div className='bg-gray-200 rounded-lg shadow-lg  p-5'>ReactJS</div>
            <div className='bg-gray-200 rounded-lg shadow-lg  p-5'>SASS</div>
            <div className='bg-gray-200 rounded-lg shadow-lg  p-5'>TailWind CSS</div>
            <div className='bg-gray-200 rounded-lg shadow-lg  p-5'>Many React Libraries</div>
            <div className='bg-gray-200 rounded-lg shadow-lg  p-5'>REST API</div>
            <div className='bg-gray-200 rounded-lg shadow-lg  p-5'>NodeJS</div>
            <div className='bg-gray-200 rounded-lg shadow-lg  p-5'>ExpressJS</div>
            <div className='bg-gray-200 rounded-lg shadow-lg  p-5'>MongoDB</div>
        </div>
      </div>

      <div className='mt-20'>
        <h1 className='text-5xl font-bold mb-20'>See Live</h1>
        <NavLink to='https://github.com/3issajr/Bistro-Bliss' className='uppercase bg-custom-purple text-white py-8 px-24 rounded-lg text-3xl font-bold'>Project Link</NavLink>
        <NavLink to='/home' className='ml-10 uppercase bg-white border-purple-600 border-4 text-black py-8 px-24 rounded-lg text-3xl font-bold'>go back</NavLink>
      </div>

      </div>

    </div>
   
   </div>
    </>
  )
}

export default Bistro
