import React from 'react'
import Project1 from '../../Public/Images/Untitled design.png'
import { NavLink } from 'react-router-dom'

const Projects = () => {
  return (
    <>
    <div id='projects' className='flex flex-col justify-center p-48'>

        <div id='header' className='text-center'>
                <h1 className='text-7xl font-bold font-sans uppercase'>projects</h1>
                <div className='flex justify-center'>
                    <hr className='bg-custom-purple h-5 w-16 m-10 rounded-lg text-2xl'/>
                </div>
            <div id='about-paragraph'>
                <p className='text-4xl text-gray-600 font-sans leading-snug'>Here you will find some of the personal and clients projects that I created with each project <br/> containing its own case study</p>
            </div>
        </div>

        <div className=' flex justify-center p-64'>
            <div className='w-1/2'>
                <img src={Project1} className='w-full'/>
            </div>
            <div className='w-1/2 flex flex-col justify-center pl-44'>
                <h1 className='text-7xl italic font-bold'>Bistro Bliss</h1>
                <p className='text-gray-600 font-sans w-4/5 text-3xl text-justify my-10 leading-snug'>Bistro Bliss is considered to be my first project that I created which have been featured on some of the biggest tech sites like Tailwind, React Libraries and etc.</p>
                <NavLink to='/bistro' className='uppercase bg-custom-purple text-white p-8 w-1/2 rounded-lg text-3xl font-bold text-center'>case study</NavLink>
            </div>
        </div>

    </div>
    </>
  )
}

export default Projects
