import React from 'react'

const Contact = () => {
  return (
    <>
    <div id='contact' className='p-48 bg-gray-100'>

    <div id='header' className='text-center'>
            <h1 className='text-7xl font-bold font-sans uppercase'>contact</h1>
            <div className='flex justify-center'>
                <hr className='bg-custom-purple h-5 w-16 m-10 rounded-lg text-2xl'/>
            </div>
        <div id='about-paragraph'>
            <p className='text-4xl text-gray-600 font-sans leading-snug'>Feel free to Contact me by submitting the form below and I will get back to you as soon as possible</p>
        </div>
    </div>
    
    <div id='contact-form' className='flex justify-center w-full  mt-16'>
      <form className='w-1/2 bg-white shadow-lg border-2  p-10'>

        <div className='flex flex-col p-10 gap-5'>
            <label className='text-4xl font-semibold text-gray-500'>Name</label>
            <input type='text' placeholder='Enter Your Name' className='bg-gray-100 rounded-lg p-10 text-2xl font-semibold'/>
        </div>

        <div className='flex flex-col p-10 gap-5'>
            <label className='text-4xl font-semibold text-gray-500'>Email</label>
            <input type='email' placeholder='Enter Your Email' className='bg-gray-100 rounded-lg p-10 text-2xl font-semibold'/>
        </div>

        <div className='flex flex-col p-10 gap-5'>
            <label className='text-4xl font-semibold text-gray-500'>Messages</label>
            <textarea cols={10} rows={10} placeholder='Enter Your Message' className='bg-gray-100 rounded-lg p-10 text-2xl font-semibold'/>
        </div>

        <div className='flex justify-end'>
            <button className='uppercase bg-custom-purple text-white p-8 w-1/3 rounded-lg text-3xl font-bold mt-16'>Submit</button>
        </div>

      </form>
    </div>


    </div>
    </>
  )
}

export default Contact
