import React from 'react'
import { faInstagram , faGithub , faLinkedin  } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Slide } from 'react-awesome-reveal'
import {RotateLoader} from 'react-spinners'

const Home = () => {
  return (
    <>
    <div className='flex flex-col justify-center bg-gradient-to-r from-gray-100 to-blue-400  p-64 shadow-md'>

    <Slide direction='left' duration={2000} className='absolute left-0'>
        <div id='socialmedia-icons' className='flex flex-col  text-7xl bg-white p-10  gap-7 shadow-lg'>
            <Slide direction='left'  duration={2000}>
                <a href="https://www.instagram.com/3issajr?igsh=NThkejFmOGppbzI0"><FontAwesomeIcon icon={faInstagram}/></a>
            </Slide>
            <Slide direction='left' duration={2000}>
                <a href="https://github.com/3issajr"><FontAwesomeIcon icon={faGithub}/></a>
            </Slide>
            <Slide direction='left' duration={2000}>
                <a href="https://www.linkedin.com/in/ahmed-eissa99"><FontAwesomeIcon icon={faLinkedin}/></a>
            </Slide>
        </div>
    </Slide>


        <div id='intro' className='text-center'>
            <h1 className='text-7xl uppercase font-extrabold'>hey, i'm Ahmed Eissa</h1>
            <p className='text-5xl p-20 font-sans'>A Fullstack Web Developer building and managing Websites and Web <br/> Applications that leads to the success of the overall product</p>
            <a href="#projects" className='bg-custom-purple text-white py-8 px-24 rounded-lg text-4xl font-bold'>PROJECTS</a>
        </div>

        <div className='text-center  mt-32'>
            <RotateLoader/>
        </div>

    </div>
    </>
  )
}

export default Home
